import { dbService, storageService } from "fbase";
import React, { useState } from "react";
import { doc, deleteDoc, updateDoc } from "firebase/firestore";
import { deleteObject, ref } from "firebase/storage";
function Tweet({ tweetObj, isOwner }) {
  const [editing, setEditing] = useState(false);
  const [newTweet, setNewTweet] = useState(tweetObj.text);
  const onDeleteClick = () => {
    const ok = window.confirm("정말로 삭제하시렵니까");
    if (ok) {
      // delete
      const tweetTextRef = doc(dbService, "tweets", `${tweetObj.id}`);
      deleteDoc(tweetTextRef);

      // 사진도 같이 지워줘야해
      const storageRef = ref(storageService, tweetObj.downloadURL);
      deleteObject(storageRef);
    } else {
    }
  };

  const toggleEditing = () => {
    setEditing((prev) => !prev);
  };

  const onSubmit = (e) => {
    e.preventDefault();
    const tweetTextRef = doc(dbService, "tweets", `${tweetObj.id}`);
    updateDoc(tweetTextRef, { text: newTweet });
    setEditing(false);
  };

  return (
    <div>
      {editing ? (
        <>
          <form>
            <input
              type="text"
              placeholder="Edit your Tweet"
              value={newTweet}
              onChange={(e) => setNewTweet(e.target.value)}
              required
            ></input>
            <input type="submit" value="수정" onClick={onSubmit}></input>
          </form>
          <button onClick={toggleEditing}>취소</button>
        </>
      ) : (
        <>
          <h4>{tweetObj.text}</h4>
          {tweetObj.downloadURL != null && tweetObj.downloadURL !== "" ? (
            <img src={tweetObj.downloadURL} width="50px" height="50px" />
          ) : null}
        </>
      )}
      {isOwner && !editing && (
        <>
          <button onClick={onDeleteClick}>삭제</button>
          <button onClick={toggleEditing}>수정</button>
        </>
      )}
    </div>
  );
}

export default Tweet;
