import { authService, dbService } from "fbase";
import {
  collection,
  doc,
  getDoc,
  getDocs,
  onSnapshot,
  orderBy,
  query,
  where,
} from "firebase/firestore";
import React, { useEffect } from "react";
import { useNavigate } from "react-router-dom";
import { useState } from "react/cjs/react.development";
import Tweet from "components/Tweet";
import { updateProfile } from "firebase/auth";

function Profile({ userObject, renderTrigger }) {
  const navigate = useNavigate();
  const onLogOutClick = () => {
    authService.signOut();
    navigate("/twitterclone");
  };

  const [myTweets, setMyTweets] = useState([]);
  const getMyTweets = async () => {
    const q = query(
      collection(dbService, "tweets"),
      where("creatorId", "==", userObject.uid),
      orderBy("createdAt", "desc")
    );

    onSnapshot(q, (snapshot) => {
      //  디비 실시간 변화를 렌더링해주기 위함.( 스냅샷 )
      const tweetArr = snapshot.docs.map((doc) => ({
        id: doc.id,
        ...doc.data(),
      }));
      setMyTweets(tweetArr);
    });
  };

  useEffect(getMyTweets, []);

  // Update Profile

  const [newDisplayName, setNewDisplayName] = useState(userObject.displayName);
  const onSubmit = async (e) => {
    e.preventDefault();
    if (userObject.displayName !== newDisplayName) {
      // 유저네임 바꾸기

      await updateProfile(userObject, { displayName: newDisplayName });
      renderTrigger();
    }
  };

  return (
    <>
      <form onSubmit={onSubmit}>
        <input
          type="text"
          placeholder="Display Name"
          value={newDisplayName}
          onChange={(e) => {
            setNewDisplayName(e.target.value);
          }}
        />
        <input type="submit" value="프로필 업데이트" />
      </form>
      <span>Profile</span>
      <button onClick={onLogOutClick}>Sign Out</button>

      <div style={{ border: "1px solid black" }}>
        <h4>내 트윗들</h4>
        {myTweets.map((tweet) => (
          <Tweet
            key={tweet.id}
            tweetObj={tweet}
            isOwner={tweet.creatorId === userObject.uid}
          />
        ))}
      </div>
    </>
  );
}
export default Profile;
