import React from "react";
import { Link } from "react-router-dom";
function Navigation({ userObject }) {
  return (
    <nav>
      <ul>
        <li>
          <Link to="/twitterclone">Home</Link>
        </li>
        <li>
          <Link to="/twitterclone/profile">
            {userObject.displayName}의 프로필
          </Link>
        </li>
      </ul>
    </nav>
  );
}

export default Navigation;
